import { DefaultButton, FontIcon, IIconProps, mergeStyles } from '@fluentui/react';
import React, { useEffect, useState } from 'react';
import { SavingItem } from '../PivotMenu';


const iconClass = mergeStyles({
    fontSize: 22,
    height: 22,
    width: 22,
    margin: '2px',
    fontWeight: 'bold',
});


export interface ISavingProps {
    data: SavingItem[]
}

function Savings(props: ISavingProps) {
    const { data } = props;
    const [total, setTotal] = useState(0);
    const [showRetr, setshowRetr] = useState(false);

    const retireIcon: IIconProps = { iconName: 'BankSolid' };
    const savingIcon: IIconProps = { iconName: 'Money' };

    useEffect(() => {
        let totalNew = data
            .filter(item => showRetr ? item.isRetirement : !item.isRetirement)
            .reduce((accumulator, currentValue) => accumulator + currentValue.amount,
                0);
        setTotal(totalNew);
    }, [data, showRetr]);

    const formatMoney = (text: number) => {
        return ((`${text}`).replace(/\B(?=(?:(\d\d)+(\d)(?!\d))+(?!\d))/g, ','));
    }

    return (
        <div
            style={{
                marginTop: '5%',
                display: 'flex',
                flexWrap: 'wrap',
            }}>
            <div style={{
                width: '72%',
                backgroundColor: '#a3c05d',
                borderRadius: '10px',
                margin: '2% 12%',
                textAlign: 'center',
                alignItems: 'center',
                padding: '8%',
            }}>
                <p style={{
                    fontSize: '55px',
                    margin: '10px 2px',
                    lineHeight: '55px'
                }}>{formatMoney(total)}</p>
                <div style={{
                    display: 'flex',
                    justifyContent: 'center',
                    marginTop: '10px',
                }}>
                    <FontIcon aria-label="Train" iconName='SaveToMobile' className={iconClass} />
                    <span style={{
                        fontSize: '18px',
                        margin: '2px',
                        marginLeft: '10px',
                        lineHeight: '18px',
                        fontWeight: 'bold',
                    }}>{showRetr ? 'Retirement Funds' : 'Total Savings'}</span>
                </div>
            </div>
            <div style={{
                height: '60vh',
                width: '100%',
                overflow: 'scroll',
                display: 'flex',
                flexWrap: 'wrap',
            }}>
                {data.map((item, index) => {
                    return (
                        (showRetr ? item.isRetirement : !item.isRetirement) ? <div key={index} style={{
                            width: '38%',
                            height: '14%',
                            backgroundColor: item.colour,
                            borderRadius: '4px',
                            margin: '2% 2%',
                            textAlign: 'left',
                            alignItems: 'center',
                            padding: '2% 4%',
                        }}>
                            <p style={{
                                fontSize: '35px',
                                margin: '10px 2px',
                                lineHeight: '35px'
                            }}>{formatMoney(item.amount)}</p>
                            <div style={{
                                display: 'flex',
                            }}>
                                <FontIcon aria-label="Train" iconName={item.icon} className={iconClass} />
                                <span style={{
                                    fontSize: '18px',
                                    margin: '2px',
                                    marginLeft: '10px',
                                    lineHeight: '18px'
                                }}>{item.header}</span>
                            </div>

                        </div>
                            : undefined
                    )
                })}
            </div>
            <div
                style={{
                    display: 'flex',
                    width: '100%',
                    justifyContent: 'center',
                }}>
                <DefaultButton
                    toggle
                    checked={showRetr}
                    text={!showRetr ? 'Show Retirement Funds' : 'Show Savings'}
                    iconProps={showRetr ? savingIcon : retireIcon}
                    onClick={() => setshowRetr(!showRetr)}
                    style={{
                        margin: '4% 2%',
                        justifyContent: 'center',
                    }}
                />
            </div>
        </div>
    )
}

export default Savings;