import * as React from 'react';
import { Pivot, PivotItem } from '@fluentui/react';
import Savings from '../Savings';
import Editor, { colorCells } from '../Editor';
import { useEffect, useState } from 'react';

export interface SavingItem {
    header: string,
    amount: number,
    icon: string,
    colour: string,
    isRetirement?: boolean,
}




export const PivotMenu: React.FunctionComponent = () => {

    const [savingsData, setSavingsData] = useState<SavingItem[]>([]);

    const saveCurrentData = (data: SavingItem[]) =>{
        const dataJson = JSON.stringify(data);
        localStorage.setItem('savingData', dataJson);
    }

    useEffect(()=>{
        const savedDataString: string | null =  localStorage.getItem('savingData');
        const savedData: SavingItem[] = savedDataString ? JSON.parse(savedDataString) : [];
        setSavingsData(savedData);
    },[])

    const changeAttribute = (index: number, key: string, value: string | number) => {
        let currentObj = savingsData[index];

        const newSavingsData = [
            ...savingsData.slice(0, index),
            {
                ...currentObj,
                [key]: value,
            },
            ...savingsData.slice(index + 1)
        ]

        setSavingsData(newSavingsData);
        saveCurrentData(newSavingsData);
    }

    const deleteRow = (index: number) => {

        const newSavingsData = [
            ...savingsData.slice(0, index),
            ...savingsData.slice(index + 1)
        ]

        setSavingsData(newSavingsData);
        saveCurrentData(newSavingsData);
    }

    const addRow = () => {

        const newRowData: SavingItem = {
            header: '',
            amount: 0,
            icon: 'AllCurrency',
            colour: colorCells[(savingsData.length % colorCells.length)].color,
            isRetirement: false
        };

        const newSavingsData = [
            ...savingsData,
            newRowData
        ]

        setSavingsData(newSavingsData);
        saveCurrentData(newSavingsData);
    }

    return (
        <Pivot aria-label="Basic Pivot Example">
            <PivotItem headerText="Savings">
                <div style={{
                    height: '100%',
                    width: '100%',
                }}>
                    <Savings data={savingsData} />
                </div>

            </PivotItem>
            <PivotItem headerText="Edit" >
                <Editor data={savingsData} onChangeAttr={changeAttribute} onAddRow={addRow} onDelete={deleteRow}/>
            </PivotItem>
        </Pivot>
    );
};